import * as hash from 'hash-sum'

export const makeElement = function (type, attributes = {}, childNodes = []) {
  const element = document.createElement(type)
  for (const attributeKey in attributes) {
    const attributeValue = attributes[attributeKey]
    if (attributeKey === 'styles') {
      for (const styleKey in attributeValue) {
        element.style.setProperty(styleKey, attributeValue[styleKey])
      }
    } else {
      element.setAttribute(attributeKey, attributeValue)
    }
  }
  childNodes.forEach((node) =>{
    if (node) {
      element.appendChild(node)
    }
  })
  return element
};

export const makeAmazonSearchUrl = function (query) {
  query = query.replace(/[\s]/g, '+')
  return `https://www.amazon.com/s?k=${query}`
};

export const getAmazonLinkInfo = function ({host, pathname, search}) {
  const domain = (host.match(/[^.]+\.[^.]+$/) || [])[0]
  let type = 'external'
  // Ad URL
  if (domain === 'amazon-adsystem.com') {
    type = 'ad'
  } else
  // Redirect URL
  if (domain === 'amazon.com' && new RegExp('^/([^/]*/)?slredirect/').test(pathname)) {
    type = 'sponsored'
  } else
  // Product URL
  if (domain === 'amazon.com' && new RegExp('^/([^/]*/)?dp/').test(pathname)) {
    type = 'product'
  } else
  // Search URL
  if (domain === 'amazon.com' && new RegExp('^/s/').test(pathname)) {
    type = 'search'
  } else
  // Category URL
  if (domain === 'amazon.com' && new RegExp('^/([^/]*/)?b/').test(pathname)) {
    type = 'category'
  } else
  if (domain === 'amazon.com') {
    type = 'amazon'
  }
  let ref = (pathname.match(/ref=(.+)/) || [])[1]
  return {
    type,
    domain,
    ref
  }
};

export const getPageType = function (selectors) {
  const pageTypes = ['home', 'search', 'product', 'cart', 'thankyou']
  return pageTypes.find((pageType) => {
    const path = selectors[`${pageType}PagePathRegex`] || null
    const isPathMatching = location.pathname.match(path) !== null
    const container = document.querySelector(selectors[`${pageType}PageContainer`])
    const requirements = selectors[`${pageType}PageRequirements`]
    if (requirements && (isPathMatching || container)) {
      return requirements.every((requirement) => {
        return document.querySelector(requirement)
      })
    }
    return false
  })
};

export const generateQueryKey = function (query) {
  return hash(query)
};

export const buildQueryBundle = function (queries, queryType) {
  const bundle = {
    queries: {
      [queryType]: {},
    },
  }
  queries.forEach((query) => {
    if (query) {
      const queryKey = generateQueryKey(query)
      bundle.queries[queryType][queryKey] = query
    }
  })
  return bundle
};

export const setLogPrefix = function (log, prefixString) {
  const logFactory = log.methodFactory
  log.methodFactory = function (methodName, logLevel, loggerName) {
    const rawMethod = logFactory(methodName, logLevel, loggerName)
    return function (...message) {
      rawMethod(prefixString, ...message)
    }
  }
  log.setLevel(log.getLevel())
};
