import {
  getHasPurchased,
  sendMessage,
  loadFullConfig,
  loadQuery,
  searchCategory,
  track,
} from '../messages'
import {
  buildQueryBundle,
  generateQueryKey,
  getAmazonLinkInfo,
  getPageType,
  makeAmazonSearchUrl,
  makeElement,
  setLogPrefix
} from '../utils'
import uniq from 'lodash/uniq'
import intersection from 'lodash/intersection'
import * as log from 'loglevel'

setLogPrefix(log, "Lustre:")

let siteName = ''
let pageType = ''

const trackError = function (error, ...errorParameters) {
  log.error(`site: "${siteName}"`, `page: "${pageType}"`,
    '\n', error,
    '\n', ...errorParameters)
  track('extension error', {
    eventCategory: `${siteName} ${pageType}`,
    eventLabel: error.message,
    errorStack: error.stack,
    errorParameters
  })
};

(async () => {
  const {
    ALLOW_DISABLED_CATEGORIES,
    BATCH_HOSTNAME,
    BUILD_TARGET,
    CATEGORY_BANNER_ENABLED,
    SEARCH_BUTTONS_WITHOUT_CATEGORY_ENABLED,
    POPUP_ON_HOVER_ENABLED,
    PRODUCT_BADGE_STATES,
    PRODUCT_INFO_ENABLED,
    PRODUCT_INFO_SHOW_NO_DATA,
    SEARCH_BUTTONS_ENABLED,
    SEARCH_BUTTONS_SHOW_NO_DATA,
    SITE_HOSTNAME,
    SITE_SELECTORS,
    TOP_DEALS_BANNER_ENABLED,
  } = await loadFullConfig()

  if (BUILD_TARGET === "production") {
    log.setLevel(log.levels.WARN, false)
  } else {
    log.setLevel(log.levels.TRACE, false)
  }

  const attachNode = function (newNode, attachmentNodes) {
    let {parentNode, precedingNode, followingNode} = attachmentNodes
    newNode.setAttribute('data-lustre-attached', '')
    parentNode = followingNode ? followingNode.parentElement : parentNode
    parentNode = precedingNode ? precedingNode.parentElement : parentNode
    const referenceNode = precedingNode ? precedingNode.nextSibling : followingNode
    if (parentNode) {
      parentNode.insertBefore(newNode, referenceNode)
      return parentNode
    } else {
      trackError(new Error(`Unable to attach node: "${newNode.className}"`))
    }
  }

  const resizeFrame = function (event) {
    const frameNode = document.querySelector(`iframe[src="${event.data.source}"]`)
    frameNode.style.height = `${frameNode.clientHeight}px`
    setTimeout(() => {
      frameNode.style.height = `${event.data.height}px`
      frameNode.style.opacity = `1`
    }, 0)
  }

  let activeButtonNode = null
  let activePopupNode = null
  const attachPopup = function (categoryName, productNodeId, productPrice, priceIntent, searchParam, isWinner, score) {
    return async (event) => {
      if (!categoryName || !productNodeId) { return }
      let targetNode = event.currentTarget || event.target
      let previousActiveButtonNode = activeButtonNode
      if (previousActiveButtonNode === targetNode) {
        if (POPUP_ON_HOVER_ENABLED) {
          event.stopPropagation()
        }
        return
      }
      removeActivePopup()
      activeButtonNode = targetNode
      let iframeNode
      let loadingNode
      let popupTailNode
      const priceIntentQueryParam = priceIntent ? `&price_intent=${priceIntent}` : ''
      const productPriceQueryParam = productPrice ? `&product_price=${productPrice}` : ''
      const hasPurchasedString = await getHasPurchased() ? '&ext_usr_ps=true' : ''
      activePopupNode = makeElement('div', {
        class: 'lux-Popup',
        'data-testid': 'product-popup',
      }, [
        makeElement('div', {
          class: 'lux-Popup-Content',
        }, [
          loadingNode = makeElement('div', {
            class: 'lux-FrameLoading',
          }, [
            makeElement('img', {
              src: chrome.runtime.getURL('/assets/images/icons/logo.svg'),
              class: 'lux-ModalLoading-Heart lux-ModalLoading-Heart_medium',
            }),
          ]),
          iframeNode = makeElement('iframe', {
            class: 'lux-Popup-Frame',
            style: 'opacity: 0;',
            src: `${SITE_HOSTNAME}/plugin/product?category_name=${categoryName}&product_node_id=${productNodeId}${priceIntentQueryParam}${productPriceQueryParam}&store=${siteName}${hasPurchasedString}`,
            id: 'lux-product-popup',
            title: 'Lustre product popup',
          }),
        ]),
        popupTailNode = makeElement('div', {
          class: 'lux-Popup-Tail',
        }),
      ])
      attachNode(activePopupNode, {parentNode: document.querySelector('body')})
      iframeNode.addEventListener('load', (event) => {
        getFrameDimensions(iframeNode)
      })
      const activePopupPosition = activePopupNode.getBoundingClientRect()
      const bounds = activeButtonNode.getBoundingClientRect()
      const buttonLeftOffset = window.scrollX + bounds.x + (bounds.width / 2) - (activePopupPosition.width / 2)
      const buttonTopOffset = window.scrollY + bounds.y + bounds.height
      const leftOffsetMax = document.querySelector('body').clientWidth - (activePopupPosition.width)
      const leftOffset = Math.min(Math.max(buttonLeftOffset, 0), leftOffsetMax)
      const topOffset = buttonTopOffset
      const tailOffset = window.scrollX + bounds.x + (bounds.width / 2) - leftOffset
      activePopupNode.setAttribute('style', `left: ${leftOffset}px; top: ${topOffset}px;`)
      popupTailNode.setAttribute('style', `left: ${tailOffset}px;`)
      const badgeConfig = getProductBadgeConfig({ isWinner, score, isFallback: false })
      const buttonLabel = badgeConfig && badgeConfig.text
      track('badge popup shown', {
        eventCategory: `${siteName} ${pageType}`,
        eventLabel: buttonLabel,
        badgeLabel: buttonLabel,
        categoryName: categoryName,
        productNodeId: productNodeId,
        searchParam: searchParam
      })
      event.stopPropagation()
      return false
    }
  }

  const removeActivePopup = function (event) {
    if (activePopupNode) {
      activePopupNode.remove()
      activePopupNode = null
    }
    if (activeButtonNode) {
      activeButtonNode = null
    }
  }

  let activeModalNode = null

  const attachModal = function (event) {
    removeActivePopup()
    let targetNode = event.currentTarget
    if (activeModalNode) {
      removeModal(activeModalNode)
    }
    let loadingNode
    let iframeNode
    let closeButtonNode
    const url = new URL(event.data.url)
    url.searchParams.set('store', siteName)
    activeModalNode = makeElement('div', {
      class: 'lux-Modal',
    }, [
      loadingNode = makeElement('div', {
        class: 'lux-ModalLoading',
      }, [
        makeElement('img', {
          src: chrome.runtime.getURL('/assets/images/icons/logo.svg'),
          class: 'lux-ModalLoading-Heart',
        })
      ]),
      iframeNode = makeElement('iframe', {
        class: 'lux-Modal-Frame',
        src: url.toString(),
      }),
      closeButtonNode = makeElement('div', {class: 'lux-Modal-CloseButton'}, [
        document.createTextNode('×')
      ]),
    ])
    const body = document.querySelector('body')
    attachNode(activeModalNode, {parentNode: body})
    body.classList.add('lux-Modal_hasModal')
    closeButtonNode.addEventListener('click', (event) => {
      removeModal(activeModalNode)
    })
    iframeNode.addEventListener('load', (event) => {
      loadingNode.remove()
    })
    track('modal shown', {
      eventCategory: `${siteName} ${pageType}`,
      eventLabel: event.data.url,
      src: event.data.url,
    })
  }

  const removeModal = function (event) {
    const body = document.querySelector('body')
    body.classList.remove('lux-Modal_hasModal')
    activeModalNode.remove()
    activeModalNode = null
  }


  const replaceModal = function (event) {
    if (activeModalNode) {
      const activeIFrameNode = activeModalNode.querySelector('iframe.lux-Modal-Frame')
      if (activeIFrameNode) {
        activeIFrameNode.setAttribute('src', event.data.url)
        return
      }
    }
    attachModal(event)
  }

  const getFrameDimensions = function (iframeNode) {
    iframeNode.contentWindow.postMessage({type: 'getDimensions', source: iframeNode.src}, '*');
  }

  const watchFrameDimensions = function (iframeNode) {
    getFrameDimensions(iframeNode)
    window.addEventListener('resize', getFrameDimensions.bind(this, iframeNode))
  }

  const applyFrameFilters = function (event) {
    const pluginFrames = [...document.querySelectorAll('iframe[src*="/plugin/"]')]
    const specFilters = event.data.specFilters
    pluginFrames.forEach((iframeNode) => {
      iframeNode.contentWindow.postMessage({type: 'applyFilters', specFilters: specFilters}, '*');
    })
    const filterStateNodes = document.querySelectorAll('.lux-FilterState')
    if (specFilters.length) {
      filterStateNodes.forEach((filterStateNode) => {
        filterStateNode.classList.add('lux-FilterState_enabled')
      })
    } else {
      filterStateNodes.forEach((filterStateNode) => {
        filterStateNode.classList.remove('lux-FilterState_enabled')
      })
    }
  }

  const navigateToAmazonSearchPage = function (query) {
    const searchUrl = makeAmazonSearchUrl(query)
    window.location.href = searchUrl
  }

  // Reset page to clean state
  const resetPage = function () {
    log.debug(`Resetting page`)
    const priorAttachedNodes = [...document.querySelectorAll('[data-lustre-attached]')]
    priorAttachedNodes.forEach((node) => {
      node.remove()
    })
    const priorModifiedNodes = [...document.querySelectorAll('[class^=lux-')]
    priorModifiedNodes.forEach((node) => {
      node.classList.forEach((classString) => {
        if (classString.indexOf('lux-') === 0) {
          node.classList.remove(classString)
        }
      })
    })
  }

  const resetComponent = function (selector) {
    log.debug(`Resetting component: ${selector}`)
    const priorAttachedNodes = [...document.querySelectorAll(selector)]
    priorAttachedNodes.forEach(node => node.remove())
  }

  const getProductBadgeConfig = function ({isWinner, score, isFallback}) {
    return PRODUCT_BADGE_STATES.find((badgeConfig) => {
      if (isWinner) {
        return badgeConfig.isWinner
      } else if (isFallback) {
        return badgeConfig.isFallback
      } else {
        const maxScore = badgeConfig.maxScore !== null ? badgeConfig.maxScore : Number.NEGATIVE_INFINITY
        const minScore = badgeConfig.minScore !== null ? badgeConfig.minScore : Number.POSITIVE_INFINITY
        return minScore < score && score <= maxScore
      }
    })
  }

  const initializePage = async function () {
    log.debug(`Initializing page`)
    resetPage()

    const disabledSites = await sendMessage('get_disabled_sites')

    log.debug(`Evaluating hostname: ${location.hostname}`)

    const domainList = location.hostname.split('.').map((value, index, list) => list.slice(index).join('.'))
    const domain = domainList.find((domain) => SITE_SELECTORS[domain])

    const currentSelectors = SITE_SELECTORS[domain]

    const disabledDomain = intersection(domainList, disabledSites)[0]
    if (disabledDomain) {
      log.debug(`Domain disabled: ${disabledDomain}`)
    }
    if (!currentSelectors) {
      log.debug(`Domain not supported: ${location.hostname}`)
    }
    // Skip for disabled and unsupported sites
    if (disabledDomain || !currentSelectors) {
      sendMessage('set_extension_state', {enabled: false, domain})
      return
    } else {
      log.debug(`Domain enabled: ${domain}`)
      sendMessage('set_extension_state', {enabled: true, domain})
    }

    siteName = currentSelectors.siteName

    const params = new URLSearchParams(location.search)
    const searchInput = document.querySelector(currentSelectors.searchInput)
    const searchParam = params.get(currentSelectors.searchParam) || (searchInput ? searchInput.value : null)
    const searchAnalytics = currentSelectors.searchAnalytics

    const homePageContainer = document.querySelector(currentSelectors.homePageContainer)
    const searchPageContainer = document.querySelector(currentSelectors.searchPageContainer)
    const productPageContainer = document.querySelector(currentSelectors.productPageContainer)
    const cartPageContainer = document.querySelector(currentSelectors.cartPageContainer)
    const thankyouPageContainer = document.querySelector(currentSelectors.thankyouPageContainer)
    const orderConfirmationContainer = document.querySelector(currentSelectors.orderConfirmationContainer)

    pageType = getPageType(currentSelectors)

    if (!pageType) {
      log.debug(`Page type not detected`)
      return
    } else {
      log.debug(`Page type detected: ${pageType}`)
    }

    if (searchParam && pageType === 'search' && searchAnalytics !== false) {
      track('search', {
        eventCategory: `${siteName} ${pageType}`,
        eventLabel: searchParam,
        searchParam: searchParam
      })
    }

    const trackSearchLink = function (event) {
      const linkInfo = getAmazonLinkInfo(event.currentTarget)
      track('clicked search link', {
        eventCategory: `${siteName} ${pageType}`,
        eventLabel: linkInfo.type,
        searchParam: searchParam,
        ...linkInfo
      })
    }

    if (searchPageContainer && searchParam && pageType === 'search') {
      searchPageContainer.querySelectorAll('a[href]').forEach((element) => {
        element.addEventListener('click', trackSearchLink)
      })
    }

    const getProductSKUFromUrl = function (url_string) {
      const match = url_string.match(/\/([A-Z0-9]{10})(\/|\?|#|$)/)
      if (match) {
        return match[1]
      } else {
        return null
      }
    }

    const getProductStoreUrlFromElement = function (element) {
      // Check if current node is price node, else find it
      const priceNode = element.matches('.a-price') ? element : element.querySelector('.a-price')
      if (!priceNode) {
        return null
      }
      const priceLink = priceNode.closest('a')
      if (!priceLink) {
        return null
      }
      return decodeURIComponent(priceLink.href)
    }

    const getProductScoresLookupQueryFromElement = function (element) {
      const url_string = getProductStoreUrlFromElement(element)
      if (!url_string) {
        return null
      }
      const product_sku = getProductSKUFromUrl(url_string)
      if (!product_sku) {
        return null
      }
      return {
        store: siteName,
        product_sku,
      }
    }

    const setProductLookupState = async function (productScoresLookupBundle) {
      let lookupQueryKeys = Object.keys(productScoresLookupBundle.queries.product_scores_lookup)
      let lookups = lookupQueryKeys.reduce( (memo, queryKey) => {
        const query = productScoresLookupBundle.queries.product_scores_lookup[queryKey]
        const response = productScoresLookupBundle.responses.product_scores_lookup[queryKey]
        memo[query.product_sku] = {
          sku: query.product_sku,
          product_type_id: response.product_type_id
        }
        return memo
      }, {})
      sendMessage('set_lookups', {lookups})
    }

    const setProductPredictionState = async function (productScoresLookupBundle, enabledCategoryID) {
      let lookupQueryKeys = Object.keys(productScoresLookupBundle.queries.product_scores_lookup)
      let predictions = lookupQueryKeys.reduce( (memo, queryKey) => {
        const lookupResponse = productScoresLookupBundle.responses.product_scores_lookup[queryKey]
        const categoryScore = getBestProductScoresResponse(queryKey, productScoresLookupBundle, enabledCategoryID)
        if (categoryScore) {
          const categoryId = categoryScore.ml_category_id
          const category = productScoresLookupBundle.entities.ml_category[categoryId]
          const isWinner = categoryScore.is_winner
          const rank = isWinner ? 1 : undefined
          const score = categoryScore.score
          const badgeConfig = getProductBadgeConfig({isWinner, score, isFallback: false})
          const label = badgeConfig.text
          memo[lookupResponse.product_type_id] = {
            product_type_id: lookupResponse.product_type_id,
            category_name: category.url_slug,
            prediction_time: Date.now(),
            rank,
            score,
            label
          }
        }
        return memo
      }, {})
      sendMessage('set_predictions', {predictions})
    }

    const isCategoryEnabled = function (categoryId, bundle) {
      if (!bundle.attributes || !bundle.attributes.ml_category || !bundle.attributes.ml_category.ml_category_ready_status_obj) {
        log.warn(`Category ready obj attribute not available`)
        return false
      }
      const categoryEnabledAttrObj = bundle.attributes.ml_category.ml_category_ready_status_obj
      return categoryEnabledAttrObj && categoryEnabledAttrObj[categoryId] && categoryEnabledAttrObj[categoryId].is_ready
    }

    const isParentCategory = (categoryId, bundle) => {
      if (!bundle.attributes || !bundle.attributes.ml_category || !bundle.attributes.ml_category.ml_category_sub_category_ids) {
        throw new Error(`Sub category ids attribute not available`)
      }
      const subCategoryIds = bundle.attributes.ml_category.ml_category_sub_category_ids[categoryId]
      return subCategoryIds && subCategoryIds.length > 0
    }

    const getBestProductScoresResponse = function (productScoresLookupQueryID, bundle, preferredCategoryId) {
      const allowDisabledCategories = ALLOW_DISABLED_CATEGORIES
      const productScoresLookupResponse = bundle.responses.product_scores_lookup[productScoresLookupQueryID]
      const productCategoryScoresQueryId = productScoresLookupResponse.product_category_scores_query_id
      if (!productCategoryScoresQueryId) {
        return
      }
      const productCategoryScoresResponse = bundle.responses.product_category_scores[productCategoryScoresQueryId] || []
      let selectedScores
      // Pick the preferred cateogry if it's enabled & non-parent
      if (preferredCategoryId && !isParentCategory(preferredCategoryId, bundle) && (allowDisabledCategories || isCategoryEnabled(preferredCategoryId, bundle))) {
        selectedScores = productCategoryScoresResponse.find((scoreResponse) => {
          return scoreResponse.ml_category_id === preferredCategoryId
        })
      }
      // Pick the top enabled non-parent category if no selected category or selected category isn't in the scores response
      if (!selectedScores) {
        selectedScores = productCategoryScoresResponse.find((scoreResponse) => {
          const id = scoreResponse.ml_category_id
          return !isParentCategory(id, bundle) && (allowDisabledCategories || isCategoryEnabled(id, bundle))
        })
      }
      return selectedScores
    }

    const makeBadgeNode = function (productId, queryID) {
      return makeElement('div', {class: 'lux-Button', 'data-id': queryID || ''}, [
        makeElement('div', {class: 'lux-ButtonLoading'}, [
          makeElement('img', {
            src: chrome.runtime.getURL('/assets/images/icons/logo.svg'),
            class: 'lux-ButtonLoading-Heart lux-ModalLoading-Heart lux-ModalLoading-Heart_small',
          }),
          makeElement('img', {
            src: chrome.runtime.getURL('/assets/images/placeholder-badge.svg'),
            class: 'lux-ButtonLoadingBadge',
          })
        ]),
        makeElement('img', {
          src: `${SITE_HOSTNAME}/api/product-rating-badge?product_id=${productId}`,
          class: 'lux-Badge',
        })
      ])
    }

    const mixinSpecHideableNode = function (node) {
      node.classList.add('lux-FilterState-Hideable')
      node.classList.add('lux-FilterState-Hideable_onEnable')
      return node
    }

    const bindButtonEvents = function (buttonNode, categoryName, productNodeId, productPrice, priceIntent, isWinner, score) {
      let timeoutId = null
      const attachHandler = attachPopup(categoryName, productNodeId, productPrice, priceIntent, searchParam, isWinner, score)
      buttonNode.addEventListener('click', attachHandler)
      if (POPUP_ON_HOVER_ENABLED) {
        buttonNode.addEventListener('mouseenter', (event) => {
          if (!activeButtonNode) {
            clearTimeout(timeoutId)
            timeoutId = setTimeout(() => {
              if (!activeButtonNode) {
                attachHandler(event)
              }
            }, 500)
          }
        })
        buttonNode.addEventListener('mouseleave', (event) => {
          clearTimeout(timeoutId)
        })
      }
    }

    // Search page features

    const getEnabledCategoryId = function (categorySearchBundle, aliasName) {
      let categoryId
      const categoryIDs = Object.keys(categorySearchBundle.entities.ml_category)
      if (categorySearchBundle.attributes && categorySearchBundle.attributes.ml_category) {
        categoryId = categoryIDs.find((id) => {
          return isCategoryEnabled(id, categorySearchBundle)
        })
      }
      // allow disabled categories on dev
      if (ALLOW_DISABLED_CATEGORIES) {
        const categorySearch = categorySearchBundle.responses.category_search
        const categorySearchKey = Object.keys(categorySearch)[0]
        // find a match between category name and search term
        const matchedCategoryId = categoryIDs.find((id) => {
          const name = categorySearchBundle.entities.ml_category[id].name
          return name && name.toLowerCase() === aliasName.toLowerCase()
        })
        if (matchedCategoryId) {
          categoryId = matchedCategoryId
        }
        // fallback to first category id
        if (!categoryId) {
          categoryId = categorySearch[categorySearchKey].category_ids[0]
        }
      }
      return categoryId
    }

    if (homePageContainer && pageType === 'home') {
      log.debug(`Initialize home page features`)
      if (TOP_DEALS_BANNER_ENABLED && (currentSelectors.topDealsPrecedingSibling || currentSelectors.topDealsFollowingSibling)) {
        log.debug(`Initialize top deals feature`)
        let iframeDealsNode
        const hasPurchasedString = await getHasPurchased() ? '&ext_usr_ps=true' : ''
        // Check for previously attached banners and remove them just in case. There can only be one.
        resetComponent('.lux-Banner')
        const topDealsNode = makeElement('div', {
          class: 'lux-Banner lux-FrameContainer',
          styles: currentSelectors.topDealsContainerStyles,
        }, [
          makeElement('div', {
            class: 'lux-FrameLoadingImage',
          }, [
            makeElement('img', {
              src: chrome.runtime.getURL('/assets/images/logo-load-in.gif'),
            }),
          ]),
          iframeDealsNode = makeElement('iframe', {
            class: 'lux-Banner-Frame',
            src: `${SITE_HOSTNAME}/plugin/top-deals?store=${siteName}${hasPurchasedString}`,
            id: 'lux-deals-banner',
            title: 'Lustre deals banner',
          }),
        ])
        iframeDealsNode.addEventListener('load', (event) => {
          watchFrameDimensions(iframeDealsNode)
          topDealsNode.classList.add('loaded')
        })
        log.debug(`Attach top deals feature`)
        attachNode(topDealsNode, {
          precedingNode: homePageContainer.querySelector(currentSelectors.topDealsPrecedingSibling),
          followingNode: homePageContainer.querySelector(currentSelectors.topDealsFollowingSibling),
        })
      }
    }

    if (searchPageContainer && pageType === 'search' && !searchParam) {
      track('search parameter unknown', {
        eventCategory: `${siteName} ${pageType}`,
        url: window.location.href,
        searchParam: searchParam,
      })
    }

    if (searchPageContainer && pageType === 'search' && searchParam) {
      log.debug(`Initialize search page features`)
      const categorySearchBundle = await searchCategory(searchParam)
      const enabledCategoryID = getEnabledCategoryId(categorySearchBundle, searchParam)
      let categoryName
      if (enabledCategoryID) {
        const categoryEntity = categorySearchBundle.entities.ml_category[enabledCategoryID]
        categoryName = categoryEntity && categoryEntity.url_slug
        searchPageContainer.classList.add('lux-FilterState')
      }

      // Add category banner feature
      if (CATEGORY_BANNER_ENABLED && categoryName &&
          (currentSelectors.bannerFollowingSibling || currentSelectors.bannerPrecedingSibling)
      ) {
        log.debug(`Initialize category banner feature`)
        const specFilterString = (() => {
          const responses = categorySearchBundle.responses.category_search
          const key = Object.keys(responses)[0]
          const response = responses[key]
          const categoryIndex = response.category_ids.indexOf(enabledCategoryID)
          const specFiltersList = response.spec_filters_list || []
          const specFilters = specFiltersList[categoryIndex] || []
          if (specFilters.length) {
            return `&spec_filters=${encodeURIComponent(JSON.stringify(specFilters))}`
          } else {
            return params.get('spec_filters') ? `&spec_filters=${encodeURIComponent(params.get('spec_filters'))}` : ''
          }
        })()
        const pagePath = isParentCategory(enabledCategoryID, categorySearchBundle) ?
          'parent_category' : 'category'
        const hasPurchasedString = await getHasPurchased() ? '&ext_usr_ps=true' : ''
        let iframeCategoryNode
        // Check for previously attached banners and remove them just in case. There can only be one.
        resetComponent('.lux-Banner')
        const categoryBannerNode = makeElement('div', {
          class: 'lux-Banner lux-FrameContainer',
          styles: currentSelectors.bannerContainerStyles,
        }, [
          makeElement('div', {
            class: 'lux-FrameLoadingImage',
          }, [
            makeElement('img', {
              src: chrome.runtime.getURL('/assets/images/logo-load-in.gif'),
            }),
          ]),
          iframeCategoryNode = makeElement('iframe', {
            class: 'lux-Banner-Frame',
            src: `${SITE_HOSTNAME}/plugin/${pagePath}?category_name=${categoryName}${specFilterString}&store=${siteName}${hasPurchasedString}`,
            id: 'lux-category-banner',
            title: 'Lustre category banner',
          }),
        ])
        iframeCategoryNode.addEventListener('load', (event) => {
          watchFrameDimensions(iframeCategoryNode)
          categoryBannerNode.classList.add('loaded')
        })
        log.debug(`Attach category banner feature`)
        attachNode(categoryBannerNode, {
          precedingNode: searchPageContainer.querySelector(currentSelectors.bannerPrecedingSibling),
          followingNode: searchPageContainer.querySelector(currentSelectors.bannerFollowingSibling),
        })
        track('category banner shown', {
          eventCategory: `${siteName} ${pageType}`,
          eventLabel: categoryName,
          categoryName: categoryName,
          searchQuery: searchParam, // This should eventually be deleted in favor of searchParam
          searchParam: searchParam
        })

        // Add disable button features
        if (currentSelectors.disableButtonFollowingSibling || currentSelectors.disableButtonPrecedingSibling) {
          log.debug(`Initialize disable button feature`)
          let disableButtonTargetNode
          const disableButtonNode = makeElement('div', {
            class: 'lux-DisableButton',
            styles: currentSelectors.disableButtonContainerStyles,
            'data-testid': 'disable-banner-button',
          }, [
            document.createTextNode(`Disable Lustre on ${currentSelectors.displayName}`),
            disableButtonTargetNode = makeElement('div', {
              class: 'lux-DisableButton-Target',
              'data-testid': 'disable-banner-button-target',
            }, [
              makeElement('img', {
                class: 'lux-DisableButton-Icon lux-DisableButton-Icon_noHover',
                src: chrome.runtime.getURL('/assets/images/icons/close.svg'),
              }),
              makeElement('img', {
                class: 'lux-DisableButton-Icon lux-DisableButton-Icon_hover',
                src: chrome.runtime.getURL('/assets/images/icons/close-solid.svg'),
              }),
            ]),
          ])
          log.debug(`Attach disable button feature`)
          attachNode(disableButtonNode, {
            precedingNode: searchPageContainer.querySelector(currentSelectors.disableButtonPrecedingSibling),
            followingNode: searchPageContainer.querySelector(currentSelectors.disableButtonFollowingSibling),
          })
          disableButtonTargetNode.addEventListener('click', (event) => {
            sendMessage('add_disabled_site', {domain: domain})
            resetPage()
            track('clicked disable on site button', {
              eventCategory: `${siteName} ${pageType}`,
              eventLabel: siteName,
              siteName: siteName,
              categoryName: categoryName,
              searchParam: searchParam
            })
          })
        }
      }

      // Add search badge button features
      if (SEARCH_BUTTONS_ENABLED && (enabledCategoryID || SEARCH_BUTTONS_WITHOUT_CATEGORY_ENABLED) && currentSelectors.searchPrice) {
        log.debug(`Initialize search page badge feature`)
        const getPriceRowNode = (priceNode) => priceNode.closest('.a-row')
        let priceNodes = [...searchPageContainer.querySelectorAll('.a-price, .a-color-price')].filter(getPriceRowNode)
        const productScoresLookupQueries = priceNodes.map(getProductScoresLookupQueryFromElement)
        const priceRowNodes = uniq(priceNodes.map(getPriceRowNode))
        const productIdsLookupPromise = loadQuery(
          BATCH_HOSTNAME,
          'product_id_lookup',
          buildQueryBundle(productScoresLookupQueries, 'product_id_lookup')
        ).then((productIdLookupBundle) => {
          priceRowNodes.forEach((priceRowNode) => {
            if (priceRowNode && !priceRowNode.querySelector('.lux-Button')) {
              const priceClass = priceRowNode.getAttribute('class')
              priceRowNode.setAttribute('class', `${priceClass} lux-ButtonContainer`)
              const productScoresLookupQuery = getProductScoresLookupQueryFromElement(priceRowNode)
              const productScoresLookupQueryID = productScoresLookupQuery ? generateQueryKey(productScoresLookupQuery) : null
              const lookupResponses = productIdLookupBundle.responses.product_id_lookup
              const productNodeId = productScoresLookupQueryID ? lookupResponses[productScoresLookupQueryID].product_type_id : null
              // Show loading badge only for SKUs that we mapped to a product
              if (productNodeId) {
                const buttonNode = mixinSpecHideableNode(makeBadgeNode(productNodeId, productScoresLookupQueryID))
                attachNode(buttonNode, {parentNode: priceRowNode})
              }
            }
          })
        })
        const productScoresLookupPromise = loadQuery(
          BATCH_HOSTNAME,
          'product_scores_lookup',
          buildQueryBundle(productScoresLookupQueries, 'product_scores_lookup')
        )
        Promise.all([productIdsLookupPromise, productScoresLookupPromise]).then(([productIdLookupBundle, productScoresLookupBundle]) => {
          setProductLookupState(productScoresLookupBundle)
          setProductPredictionState(productScoresLookupBundle, enabledCategoryID)
          priceRowNodes.forEach((priceRowNode) => {
            if (priceRowNode) {
              const buttonNode = priceRowNode.querySelector('.lux-Button')
              if (buttonNode) {
                const productScoresLookupQueryId = buttonNode.getAttribute('data-id')
                const productScoresLookupResponse = productScoresLookupBundle.responses.product_scores_lookup[productScoresLookupQueryId]
                const productId = productScoresLookupResponse.product_type_id
                const selectedScoresResponse = getBestProductScoresResponse(productScoresLookupQueryId, productScoresLookupBundle, enabledCategoryID)
                let selectedCategoryName = categoryName
                // Override selected category if possible
                if (selectedScoresResponse) {
                  const categoryId = selectedScoresResponse.ml_category_id
                  const selectedCategoryEntity = productScoresLookupBundle.entities.ml_category[categoryId]
                  if (selectedCategoryEntity) {
                    selectedCategoryName = selectedCategoryEntity.url_slug
                  }
                }
                const priceNode = priceRowNode.querySelector('.a-offscreen')
                let productPrice
                if (priceNode) {
                  productPrice = priceNode.textContent.replace(/\D/g, '')
                }
                const isWinner = selectedScoresResponse && selectedScoresResponse.is_winner
                const score = selectedScoresResponse && selectedScoresResponse.score
                if (score) {
                  bindButtonEvents(buttonNode, selectedCategoryName, productId, productPrice, null, isWinner, score)
                } else {
                  buttonNode.remove()
                }
              }
            }
          })
          log.debug(`Attach search page badge feature`)
          if (priceRowNodes.find((priceRowNode) => priceRowNode && priceRowNode.querySelector('.lux-Button'))) {
            track('product badges shown', {
              eventCategory: `${siteName} ${pageType}`,
              eventLabel: categoryName,
              categoryName: categoryName,
              searchParam: searchParam
            })
          }
        })
      }
    }

    // Product page features

    if (productPageContainer && pageType === 'product') {
      log.debug(`Initialize product page features`)
      // Add Product to state
      const priceNode = document.getElementById('priceblock_dealprice') || document.getElementById('priceblock_ourprice') || document.getElementById('priceblock_saleprice') || document.getElementById('price_inside_buybox')
      let productPrice = null
      if (priceNode) {
        const productPriceText = priceNode.textContent.trim()
        productPrice = parseFloat(productPriceText.replace(/[$,]/g, ''))
      }
      // TODO - we should try to use the normalizer logic for extracting SKUs from URLs
      // TODO - we want to use:
      // const productASIN = document.querySelector('[rel=canonical]').getAttribute('href').match(/\/(?:dp|gp\/product)\/([^/?#]+)/)
      const productASIN = getProductSKUFromUrl(location.pathname)
      if (productASIN) {
        let products = {
          [productASIN]: {
            sku: productASIN,
            store: siteName,
            price: productPrice
          }
        }
        sendMessage('set_products', {products})
        // Add Product Information Frame
        if (PRODUCT_INFO_ENABLED) {
          log.debug(`Initialize product info feature`)
          let categoryName = params.get('category_name')
          const productLookupQueryContent = {
            store: siteName,
            product_sku: productASIN,
          }
          const productScoresLookupQueryBundle = buildQueryBundle([productLookupQueryContent], 'product_scores_lookup')
          const productScoresLookupQueryId = Object.keys(productScoresLookupQueryBundle.queries.product_scores_lookup)[0]
          const productScoresLookupPromise = loadQuery(
            BATCH_HOSTNAME,
            'product_scores_lookup',
            productScoresLookupQueryBundle
          )
          const productScoresLookupBundle = await productScoresLookupPromise
          // Find the category entity based on the passed in category name
          const selectedCategory = Object.values(productScoresLookupBundle.entities.ml_category)
            .find((category) => category.url_slug === categoryName) || {}
          let categoryId = selectedCategory.ml_category_id
          const lookupResponses = productScoresLookupBundle.responses.product_scores_lookup
          // Find the best score response or the score response based on the passed in category name
          const selectedScoresResponse = getBestProductScoresResponse(productScoresLookupQueryId, productScoresLookupBundle, categoryId)
          // Get the category id and name if not passed in
          if (selectedScoresResponse && !categoryName) {
            categoryId = selectedScoresResponse.ml_category_id
            categoryName = productScoresLookupBundle.entities.ml_category[categoryId].url_slug
          }
          // Set internal lookups for product tracking
          setProductLookupState(productScoresLookupBundle)
          setProductPredictionState(productScoresLookupBundle, categoryId)
          // Make sure a score for the product exists. This is needed in case category name was passed via
          // url param but the SKU changed due to a client side transition
          if (selectedScoresResponse && (selectedScoresResponse.score !== null || PRODUCT_INFO_SHOW_NO_DATA )) {
            const featureNode = document.querySelector('[data-feature-name="featurebullets"]')
            const isDesktop = featureNode.clientWidth >= 300
            const insertionNode = isDesktop ? featureNode : document.querySelector('#hover-zoom-end')
            const productNodeId = lookupResponses[productScoresLookupQueryId].product_type_id
            const categoryNameQueryString = `&category_name=${categoryName}`
            const productQueryString = `product_node_id=${productNodeId}`
            const priceIntent = params.get('price_intent')
            const priceIntentQueryParamString = priceIntent ? `&price_intent=${priceIntent}` : ''
            const targetProductPrice = params.get('product_price') || (productPrice && Math.floor(productPrice*100).toString())
            const productPriceQueryParamString = targetProductPrice ? `&product_price=${targetProductPrice}` : ''
            const specFilterString = params.get('spec_filters') ? `&spec_filters=${encodeURIComponent(params.get('spec_filters'))}` : ''
            const hasPurchasedString = await getHasPurchased() ? '&ext_usr_ps=true' : ''

            let buttonNode
            let iframeProductNode
            resetComponent('.lux-ProductInfo')
            const productAnalysisNode =
              makeElement('div', {
                class: 'lux-ProductInfo',
              }, [
                buttonNode = makeBadgeNode(productNodeId, productScoresLookupQueryId),
                makeElement('div', {
                  class: 'lux-ProductSnippet lux-FrameContainer',
                }, [
                  makeElement('div', {
                    class: 'lux-FrameLoading',
                  }, [
                    makeElement('img', {
                      src: chrome.runtime.getURL('/assets/images/icons/logo.svg'),
                      class: 'lux-ModalLoading-Heart lux-ModalLoading-Heart_medium',
                    }),
                  ]),
                  iframeProductNode = makeElement('iframe', {
                    class: 'lux-ProductSnippet-Frame' + (categoryName ? '' : ' lux-ProductSnippet-Frame_collapsed'),
                    style: 'opacity: 0;',
                    src: `${SITE_HOSTNAME}/plugin/product?${productQueryString}${categoryNameQueryString}${priceIntentQueryParamString}${productPriceQueryParamString}&store=${siteName}${specFilterString}${hasPurchasedString}`,
                    id: 'lux-product-snippet',
                    title: 'Lustre product snippet',
                  }),
                ])
              ])
            const productButtonClasses = ['lux-ProductSnippet-Button', 'lux-Button_noHover']
            buttonNode.classList.add(...productButtonClasses)
            log.debug(`Attach product info feature`)
            attachNode(productAnalysisNode, {followingNode: insertionNode})
            track('product info shown', {
              eventCategory: `${siteName} ${pageType}`,
              eventLabel: categoryName,
              categoryName: categoryName,
              productNodeId: productNodeId,
              productSku: productASIN
            })
            iframeProductNode.addEventListener('load', (event) => {
              watchFrameDimensions(iframeProductNode)
            })
          } else {
            log.debug(`Product info category not found`)
          }
        }
      }
    }

    // Shopping cart page features

    if (cartPageContainer && pageType === 'cart') {
      log.debug(`Initialize cart page features`)
      let cartItems = [...cartPageContainer.querySelectorAll('[data-asin]')]
      let products = cartItems.reduce( (memo, item) => {
        let productASIN = item.getAttribute('data-asin')
        let productPriceText = item.getAttribute('data-price')
        const productPrice = parseFloat(productPriceText.replace(/[$,]/g, ''))
        memo[productASIN] = {
          sku: productASIN,
          store: siteName,
          price: productPrice
        }
        return memo
      }, {})
      sendMessage('set_products', {products})
      const productScoresLookupQueryContent = Object.keys(products).map((sku) => {
        return {
          store: siteName,
          product_sku: sku,
        }
      })
      const productScoresLookupQueryBundle = buildQueryBundle(productScoresLookupQueryContent, 'product_scores_lookup')
      const productScoresLookupPromise = loadQuery(
        BATCH_HOSTNAME,
        'product_scores_lookup',
        productScoresLookupQueryBundle
      )
      const productScoresLookupBundle = await productScoresLookupPromise
      setProductLookupState(productScoresLookupBundle)
    }

    // Thank you page features

    if (thankyouPageContainer && pageType === 'thankyou') {
      log.debug(`Initialize thankyou page features`)
      const skus = orderConfirmationContainer ? [...orderConfirmationContainer.querySelectorAll('a[href]')].flatMap(
        anchor => {
          const sku = getProductSKUFromUrl(anchor.href)
          return sku ? sku : []
        }
      ) : []
      const orderIds = params.getAll('orderId')
      const purchaseIds = params.getAll('purchaseId')
      sendMessage('check_order', {skus, orderIds, purchaseIds}).then((hasOrder) => {
        if (!hasOrder) {
          track('thankyou page', {
            eventCategory: `${siteName} ${pageType}`,
            skus: skus
          })
          skus.forEach( (sku) => sendMessage('set_purchase', {sku: sku}))
        }
      })
    }
  }

  let currentLocation = location.href
  chrome.runtime.onMessage.addListener((event) => {
    if (event.type) {
      log.debug(`Received background message: ${event.type}`, event)
    }
    switch (event.type) {
      case 'tab_updated':
        if (location.href !== currentLocation) {
          log.debug(`Soft transition detected: ${currentLocation} to ${location.href}`)
          currentLocation = location.href
          initializePage().catch(trackError)
        }
        break
    }
  })

  window.addEventListener('message', (event) => {
    // Messages may come from non lustre sources and must be checked for compatibility
    if (!event.data) {
      log.debug(`Received unknown window message`, event)
      return
    }
    if (event.data.type) {
      log.debug(`Received window message: ${event.data.type}`, event.data)
    }
    switch (event.data.type) {
      case 'getDimensions':
        resizeFrame(event)
        break
      case 'attachModal':
        attachModal(event)
        break
      case 'removeModal':
        removeModal(event)
        break
      case 'replaceModal':
        replaceModal(event)
        break
      case 'navigateToAmazonSearch':
        const { query } = event.data
        if (!query) {
          break
        }
        navigateToAmazonSearchPage(query)
        break
      case 'applyFilters':
        applyFrameFilters(event)
        break
    }
  })

  // Using { capture: true } so this triggers before any other event handler because this handler mutates some state (activePopupNode, activeButtonNode)
  // and can cause issues in cases where another click handler is async but uses the pieces of state that it changes
  document.addEventListener('click', removeActivePopup, { capture: true })

  initializePage().catch(trackError)
})().catch(trackError)
